.mask{
    position: relative;
    width:100%;
    height:100vh;

}

.intropic{
    width: 100%;
    height:100%;
    background-size: cover;
    background-position: center;
    object-fit: cover;
    position: relative; /* Add this */
}

.intropic::after {
    content:"";
    width:100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: black;
    opacity:0.6;
}



.mask::after {
    content:"";
    width:100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: black;
    opacity:0.6;
}

.hero{
    height: 100%;
    width: 100%;
}

.hero .content{
    position: absolute;
    transform: translate(-50%,-50%);
    top: 50%;
    left:50%;
    text-align:center;
    display: flex; /* Add this */
    flex-direction: column; /* Add this */
    justify-content: center; /* Add this */
    align-items: center; /* Add this */

}

.hero .content h1{
    font-size:4rem;
    padding:0.6rem 0 1.5rem;
}

.hero .content p{
    font-size: 1.4rem;
    font-weight:200;
    text-transform: uppercase;
}

.content .btn{
    margin:1rem 0.2rem;
}


@media screen and  (max-width: 640px)
{
    .hero .content h1{
        font-size: 3rem;
    }

    .hero .content p{
        font-size: 1.4rem;;
    }
}

.content .btn-resume {
    margin-top: 10%; /* Adjust as needed */
    font-display: #333;
    margin:1rem 0.2rem;
    
    
}